"use client"

import type React from "react"

import { useState } from "react"
import { useRouter, useSearchParams } from "next/navigation"
import Link from "next/link"
import { motion, AnimatePresence } from "framer-motion"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card } from "@/components/ui/card"
import { useBuildBridgeStore, type UserRole } from "@/lib/store"
import { PageTransition } from "@/components/common/page-transition"
import { ArrowLeft, Check, Code2, Palette, Users } from "lucide-react"

export default function SignupPage() {
  const router = useRouter()
  const searchParams = useSearchParams()
  const initialRole = (searchParams.get("role") as UserRole) || null

  const { setIsLoggedIn, setUserName, setUserRole } = useBuildBridgeStore()
  const [step, setStep] = useState(initialRole ? 2 : 1)
  const [loading, setLoading] = useState(false)
  const [selectedRole, setSelectedRole] = useState<UserRole | null>(initialRole)
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    password: "",
    confirmPassword: "",
  })

  const roles: { id: UserRole; label: string; icon: any; description: string }[] = [
    {
      id: "labourer",
      label: "Labourer",
      icon: Palette,
      description: "Find nearby construction jobs, showcase your skills, and get paid securely for your work.",
    },
    {
      id: "builder",
      label: "Builder/Contractor",
      icon: Code2,
      description: "Hire skilled labourers, manage projects, track progress, and handle payments easily.",
    },
    {
      id: "client",
      label: "Client",
      icon: Users,
      description: "Post construction jobs, compare workers, and complete projects with transparent pricing.",
    },
  ]

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  const handleRoleSelect = (role: UserRole) => {
    setSelectedRole(role)
    setStep(2)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    if (formData.password !== formData.confirmPassword) {
      alert("Passwords do not match")
      return
    }

    setLoading(true)
    await new Promise((resolve) => setTimeout(resolve, 500))

    setIsLoggedIn(true)
    setUserName(formData.name)
    if (selectedRole) {
      setUserRole(selectedRole)
    }
    router.push("/auth/verify")
  }

  const stepVariants = {
    hidden: { opacity: 0, y: 20 },
    visible: { opacity: 1, y: 0 },
    exit: { opacity: 0, y: -20 },
  }

  return (
    <PageTransition>
      <div className="min-h-screen flex items-center justify-center py-12 px-4 sm:px-6 lg:px-8 bg-gradient-to-br from-background via-background to-primary/5">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="w-full max-w-md space-y-6"
        >
          {/* Back Button */}
          <Link
            href="/"
            className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground transition-colors"
          >
            <ArrowLeft className="h-4 w-4 mr-2" />
            Back to Home
          </Link>

          {/* Card */}
          <Card className="p-8 border border-border">
            <div className="space-y-6">
              {/* Step Indicator */}
              <div className="flex items-center justify-center gap-2">
                <div
                  className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium transition-all ${
                    step >= 1 ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"
                  }`}
                >
                  {step > 1 ? <Check className="h-4 w-4" /> : "1"}
                </div>
                <div className={`w-12 h-0.5 transition-all ${step > 1 ? "bg-primary" : "bg-muted"}`} />
                <div
                  className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium transition-all ${
                    step >= 2 ? "bg-primary text-primary-foreground" : "bg-muted text-muted-foreground"
                  }`}
                >
                  2
                </div>
              </div>

              <AnimatePresence mode="wait">
                {step === 1 ? (
                  // Step 1: Role Selection
                  <motion.div
                    key="role-selection"
                    variants={stepVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    transition={{ duration: 0.3 }}
                    className="space-y-4"
                  >
                    <div className="space-y-2">
                      <h1 className="text-2xl font-bold">What's your role?</h1>
                      <p className="text-muted-foreground">Choose what best describes you</p>
                    </div>

                    <div className="space-y-3">
                      {roles.map((role) => {
                        const Icon = role.icon
                        return (
                          <motion.button
                            key={role.id}
                            onClick={() => handleRoleSelect(role.id)}
                            whileHover={{ scale: 1.02 }}
                            whileTap={{ scale: 0.98 }}
                            className={`w-full p-4 rounded-lg border-2 text-left transition-all ${
                              selectedRole === role.id
                                ? "border-primary bg-primary/5"
                                : "border-border hover:border-primary/50"
                            }`}
                          >
                            <div className="flex items-start gap-3">
                              <Icon className="h-5 w-5 mt-0.5 text-primary" />
                              <div>
                                <h3 className="font-semibold">{role.label}</h3>
                                <p className="text-sm text-muted-foreground">{role.description}</p>
                              </div>
                            </div>
                          </motion.button>
                        )
                      })}
                    </div>

                    <Button onClick={() => setStep(2)} disabled={!selectedRole} className="w-full mt-6">
                      Continue
                    </Button>
                  </motion.div>
                ) : (
                  // Step 2: Account Details
                  <motion.div
                    key="account-details"
                    variants={stepVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    transition={{ duration: 0.3 }}
                    className="space-y-4"
                  >
                    <div className="space-y-2">
                      <h1 className="text-2xl font-bold">Create your account</h1>
                      <p className="text-muted-foreground">
                        Signing up as a <span className="font-medium capitalize">{selectedRole}</span>
                      </p>
                    </div>

                    <form onSubmit={handleSubmit} className="space-y-4">
                      <div>
                        <Label htmlFor="name">Full Name</Label>
                        <Input
                          id="name"
                          type="text"
                          name="name"
                          placeholder="John Doe"
                          value={formData.name}
                          onChange={handleChange}
                          required
                          className="mt-1.5"
                        />
                      </div>

                      <div>
                        <Label htmlFor="email">Email</Label>
                        <Input
                          id="email"
                          type="email"
                          name="email"
                          placeholder="you@example.com"
                          value={formData.email}
                          onChange={handleChange}
                          required
                          className="mt-1.5"
                        />
                      </div>

                      <div>
                        <Label htmlFor="password">Password</Label>
                        <Input
                          id="password"
                          type="password"
                          name="password"
                          placeholder="••••••••"
                          value={formData.password}
                          onChange={handleChange}
                          required
                          className="mt-1.5"
                        />
                      </div>

                      <div>
                        <Label htmlFor="confirmPassword">Confirm Password</Label>
                        <Input
                          id="confirmPassword"
                          type="password"
                          name="confirmPassword"
                          placeholder="••••••••"
                          value={formData.confirmPassword}
                          onChange={handleChange}
                          required
                          className="mt-1.5"
                        />
                      </div>

                      <div className="flex gap-3 pt-4">
                        <Button type="button" variant="outline" onClick={() => setStep(1)} className="flex-1">
                          Back
                        </Button>
                        <Button type="submit" disabled={loading} className="flex-1">
                          {loading ? "Creating..." : "Create Account"}
                        </Button>
                      </div>
                    </form>

                    <p className="text-center text-sm text-muted-foreground">
                      Already have an account?{" "}
                      <Link href="/login" className="text-primary hover:underline font-medium">
                        Sign in
                      </Link>
                    </p>
                  </motion.div>
                )}
              </AnimatePresence>
            </div>
          </Card>
        </motion.div>
      </div>
    </PageTransition>
  )
}
